/* Set the overall background color for the body */
body {
  background-color: #0a192f;
}

/* Main container for the home component */
.home-main {
  position: relative;
  display: grid;
  padding-top: 100px;
  align-items: center;
  justify-content: center;
  gap: 50px;
  grid-template-columns: 40% 40%;
  padding-inline: 80px;
}

/* Media queries for responsive design */
@media screen and (max-width: 1024px) {
  .home-main {
    gap: 80px;
  }
}

@media screen and (max-width: 768px) {
  .home-main {
    display: flex;
    flex-direction: column-reverse;
    padding-top: 60px;
    padding-bottom: 60px;
    gap: 40px;
  }
}

/* Styles for the fixed social icons on the side */
.social-icons-side {
  position: fixed;
  left: 40px;
  bottom: 0;
  z-index: 10;
}

.social-icons-side ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.social-icons-side ul::after {
  content: '';
  display: block;
  width: 1px;
  height: 90px;
  margin: 0 auto;
  background-color: #8892b0;
}

.social-icons-side li {
  margin-bottom: 20px;
}

.social-icons-side a {
  color: #8892b0;
  font-size: 20px;
  transition: all 0.3s ease;
}

.social-icons-side a:hover {
  color: #64ffda;
  transform: translateY(-3px);
}

/* Styles for the image container */
.home-image {
  padding-top: 30px;
  width: 100%;
}

.home-image img {
  max-width: 400px;
}

@media screen and (max-width: 768px) {
  .home-image {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
  }

  .home-image img {
    width: 100%;
  }
}

/* Styles for the text content */
.home-text {
  padding-top: 30px;
}

.home-text h1 {
  font-size: 3rem;
  font-weight: 700;
  color: #ccd6f6;
  font-family: "Outfit", system-ui;
  text-wrap: nowrap;
}

.home-text p {
  font-size: 1.5rem;
  font-weight: 400;
  color: #8892b0;
  font-family: "Moderustic", sans-serif;
}

/* Media queries for responsive text sizes */
@media screen and (max-width: 1024px) {
  .home-text h1 {
    font-size: 2.5rem;
  }

  .home-text p {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 768px) {
  .home-text h1 {
    font-size: 2rem;
  }

  .home-text p {
    font-size: 1.1rem;
  }
}

/* Styles for the typing animation */
.typing-animation {
  display: flex;
  align-items: center;
  gap: 5px;
}

.typing-animation .typing-text {
  color: #64ffda;
  white-space: nowrap;
  overflow: hidden;
  animation: typing 4s steps(20) infinite;
}

.typing-animation .cursor {
  animation: blink 0.7s infinite;
}

/* Keyframe animations for typing effect */
@keyframes typing {
  from { width: 0; }
  to { width: 100%; }
}

@keyframes blink {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}

/* Styles for the button container */
.button-container {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

@media screen and (max-width: 768px) {
  .button-container {
    flex-direction: row;
    justify-content: center;
  }
}

/* Common button styles */
.resume-button,
.about-button {
  padding: 10px 20px;
  font-size: 1rem;
  font-family: "Moderustic", sans-serif;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

@media screen and (max-width: 768px) {
  .resume-button,
  .about-button {
    width: auto;
    max-width: none;
  }
}

/* Specific styles for the resume button */
.resume-button {
  background-color: #0a192f;
  color: #64ffda;
  border: 2px solid #64ffda;
}

.resume-button:hover {
  background-color: #36c2ce;
  color: #000000;
}

/* Specific styles for the about button */
.about-button {
  background-color: #0a192f;
  color: #64ffda;
  border: 2px solid #64ffda;
}

.about-button:hover {
  background-color: #478ccf;
  color: white;
}

@media screen and (max-width: 768px) {
  .home-main {
    flex-direction: column-reverse;
    align-items: flex-start;
  gap: 20px;
   
  }

  .home-image{
    margin: 0px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    padding-top: 50px;
    

    img{
      max-width: 500px;
    }
  }


  .home-text{
    margin: 0px;
    padding-top: 0px;
  }


}

@media screen and (max-width: 545px) {
  .home-main {
    padding-inline: 20px;

   
  }

  .home-text{
   padding-left: 40px;
  }

}

@media screen and (max-width: 430px) {
  .home-main {
    padding-inline: 0px;

   
  }

  .home-text{
   padding-left: 40px;
  }

}