.footer {
    background-color: #0a192f;
    color: #8892b0;
    padding: 2rem;
    font-family: 'Arial', sans-serif;
    
    .footer-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      
      p {
        font-size: 0.9rem;
        margin-top: 1rem;
      }
    }
  
    .social-icons {
      display: none; // Hidden by default for larger screens
  
      a {
        color: #8892b0;
        font-size: 20px;
        margin: 0 10px;
        transition: all 0.3s ease;
  
        &:hover {
          color: #64ffda;
          transform: translateY(-3px);
        }
      }
    }
  }
  
  // Media query for mobile devices
  @media (max-width: 768px) {
    .footer {
      .social-icons {
        display: flex; // Show social icons in footer on mobile
        justify-content: center;
        margin-bottom: 1rem;
      }
    }
  
    // Hide the side social icons on mobile
    .social-icons-side {
      display: none;
    }
  }