html {
    scroll-behavior: smooth;
  }
  
  .about-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #0a192f;
    color: #8892b0;
  }
  
  .about-content-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    margin-bottom: 60px;
    width: 100%;
    padding: 100px 5%;
    padding-top: 150px;
  }
  
  .lottie-animation {
    flex: 0 0 auto;
    width: 400px;
    height: 400px; 
  }
  
  .about-content {
    flex: 1;
  
    h2 {
      font-size: 2.5em;
      font-family: "Moderustic", sans-serif;
      color: #ccd6f6;
      margin-bottom: 20px;
      position: relative;
      
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -10px;
        width: 50px;
        height: 3px;
        background-color: #64ffda;
      }
    }
  
    p {
      font-size: 1.1em;
      font-family: "Outfit", system-ui;
      line-height: 1.6;
      color: #8892b0;
      margin-bottom: 15px;
    }
  }
  
  .skills-section {
    width: 90%;
  
    h3 {
      font-size: 2em;
      font-family: "Moderustic", sans-serif;
      color: #ccd6f6;
      margin-bottom: 30px;
      text-align: center;
    }
  }
  
  @media (max-width: 1024px) {
    .lottie-animation {
      width: 300px;
      height: 300px; 
  }
  
  @media (max-width: 768px) {
    .about-content-wrapper {
      flex-direction: column;
      padding-top: 80px;

      .about-content{
        p{
          padding-top: 20px;
        }
        h2{
          display: flex;
          justify-content: center;

          &::after {
            transform: translateX(-180%);
            position: absolute;
            left: 50%;
            
          }
        }
      }
    }
  
    .lottie-animation, .about-content {
      flex: 0 0 100%;
    }
  
    .lottie-animation {
      margin-bottom: 30px;
      width: 100%;
      max-width: 400px;
      height: auto; // The height will adjust automatically to maintain aspect ratio
    }
  }
}