.contact-section {
  background-color: #0a192f;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}

.contact-container {
  max-width: 600px;
  width: 100%;
  border: 2px solid #64ffda;
  border-radius: 10px;
  padding: 40px;
  box-shadow: 0 0 20px rgba(100, 255, 218, 0.3), 0 0 40px rgba(100, 255, 218, 0.1), inset 0 0 20px rgba(100, 255, 218, 0.1);
  transition: box-shadow 0.3s ease;
}
.contact-container:hover {
  box-shadow: 0 0 25px rgba(100, 255, 218, 0.4), 0 0 50px rgba(100, 255, 218, 0.2), inset 0 0 30px rgba(100, 255, 218, 0.2);
}

.contact-content {
  text-align: center;
}

.section-number {
  font-family: "SF Mono", "Fira Code", monospace;
  font-size: 1rem;
  color: #64ffda;
  margin-bottom: 10px;
}

.section-title {
  font-family: "Calibre", "Inter", "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  font-size: 4rem;
  font-weight: 600;
  color: #ccd6f6;
  margin-bottom: 20px;
  text-shadow: 0 0 10px rgba(204, 214, 246, 0.3);
}

.section-description {
  font-family: "Calibre", "Inter", "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  font-size: 1.25rem;
  color: #8892b0;
  margin-bottom: 20px;
  line-height: 1.5;
}

.contact-button {
  display: inline-block;
  padding: 1rem 2rem;
  font-family: "SF Mono", "Fira Code", monospace;
  font-size: 0.875rem;
  color: #64ffda;
  background-color: transparent;
  border: 1px solid #64ffda;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}
.contact-button:hover {
  background-color: rgba(100, 255, 218, 0.1);
  box-shadow: 0 0 15px rgba(100, 255, 218, 0.5);
}

@media (max-width: 768px) {
  .contact-container {
    padding: 30px;
  }
  .section-title {
    font-size: 3rem;
  }
  .section-description {
    font-size: 1.1rem;
  }
}/*# sourceMappingURL=contact.css.map */