/* Apply box-sizing to all elements */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Header container styling */
.Header-class {
  display: flex; /* Use flexbox for layout */
  justify-content: space-between; /* Space out children elements */
  align-items: center; /* Center children elements vertically */
  padding: 10px 80px; /* Padding around the header */
  background-color: #0a192f; /* Background color of the header */
  position: fixed; /* Fix the header at the top */
  width: 100%; /* Full width of the viewport */
  z-index: 1000; /* Ensure the header is on top */

  /* Logo styling */
  .logo {
    img {
      width: 100px; /* Set the width of the logo image */
      height: auto; /* Maintain aspect ratio */
    }
  }
}

/* Menu overlay styling */
.menu-overlay {
  display: none; /* Hide overlay by default */
  position: fixed; /* Fix overlay to viewport */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(10, 25, 47, 0.7); /* Semi-transparent background */
  backdrop-filter: blur(5px); /* Blur effect */
  z-index: 998; /* Ensure overlay is below header */
}

/* Menu styling for mobile view */
.menu {
  @media (max-width: 768px) {
    position: fixed; /* Fix menu to viewport */
    top: 0;
    right: 0;
    width: 70%; /* Menu width */
    height: 100vh; /* Full viewport height */
    background-color: #112240; /* Background color */
    padding: 2rem; /* Padding inside menu */
    transform: translateX(100%); /* Hide menu off-screen */
    transition: transform 0.3s ease-in-out; /* Smooth transition */
    z-index: 999; /* Ensure menu is above overlay */

    &.show {
      transform: translateX(0); /* Show menu */
    }
  }

  /* Menu item styling */
  .menu-item {
    display: flex; /* Flexbox layout */
    list-style: none; /* Remove list bullets */
    gap: 20px; /* Space between items */

    @media (max-width: 768px) {
      flex-direction: column; /* Stack items vertically */
      align-items: center; /* Center items horizontally */
      justify-content: center; /* Center items vertically */
      height: 100%; /* Full height */
    }

    li {
      font-size: 1rem; /* Font size */
      font-weight: 400; /* Font weight */
      font-family: "SF Mono", "Fira Code", monospace; /* Font family */
      color: #ccd6f6; /* Text color */
      cursor: pointer; /* Pointer cursor on hover */
      display: flex; /* Flexbox layout */
      align-items: center; /* Center items vertically */
      
      &:hover {
        .menu-item-number, .menu-item-text {
          color: #64ffda; /* Change color on hover */
        }
      }

      .menu-item-number {
        color: #64ffda; /* Number color */
        margin-right: 0.5rem; /* Space after number */
        font-size: 0.9rem; /* Font size */
      }

      .menu-item-text {
        transition: color 0.3s ease; /* Smooth color transition */
      }
    }
  }
}

/* Resume button styling */
.resume-button {
  border: 2px solid #64ffda; /* Border color and width */
  padding: 10px 15px; /* Padding inside button */
  border-radius: 5px; /* Rounded corners */
  background-color: transparent; /* Transparent background */
  color: #64ffda; /* Text color */
  font-family: "SF Mono", "Fira Code", monospace; /* Font family */
  font-size: 0.9rem; /* Font size */
  cursor: pointer; /* Pointer cursor on hover */
  transition: background-color 0.3s ease, color 0.3s ease; /* Smooth transitions */

  &:hover {
    background-color: rgba(100, 255, 218, 0.1); /* Background color on hover */
  }
}

/* Hamburger menu icon styling */
.hamburger {
  display: none; /* Hide by default */
  font-size: 1.5rem; /* Font size */
  color: #64ffda; /* Icon color */
  cursor: pointer; /* Pointer cursor on hover */
  z-index: 1001; /* Ensure icon is on top */

  @media (max-width: 768px) {
    display: block; /* Show on mobile */
  }
}

/* Show menu overlay when active */
.menu-overlay.show {
  display: block; /* Display overlay */
}


@media screen and (max-width: 768px) {
  .Header-class{
    padding: 0px;
    padding-inline: 20px;
    padding-top: 25px;

    .logo{
      position: absolute;
      left: -6px;
    }

    .hamburger{
      margin-left: auto;
    }
  }
 
  
}